<template>
<div class="movie">
     <swipe class="my-swipe">
      <swipe-item class="slide1"></swipe-item>
      <swipe-item class="slide2"></swipe-item>
      <swipe-item class="slide3"></swipe-item>
      <swipe-item class="slide4"></swipe-item>
      <swipe-item class="slide5"></swipe-item>
    </swipe>
    <ul class="movie-list">
      <li v-for="movie in movieList" :key="movie.id" class="movie-con">
        <div>
          <router-link :to="'/first/firstdetail/'+movie.id">
            <img :src="movie.data.content.data.cover.feed">
            <p>{{movie.data.content.data.description}}</p>
          </router-link>
        </div>
      </li>
    </ul>
    
  </div>

</template>

<script>
import Axios from 'axios'
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";
export default {
    data(){
        return{
            movieList:[],
        }
    },
    methods:{
        loadData(){
            let url2="./static/data/shipin.json"
            Axios.get(url2).then((res)=>{
                this.movieList=res.data.result;
                console.log(this.movieList)
            }).catch(()=>
                 alert("失败")
            );
        }
    },
    mounted() {
        this.loadData();
    },
    components:{
        "swipe": Swipe,
        "swipe-item": SwipeItem
    }
}
</script>

<style scoped>
.movie-list{width: 100%;margin-top:0.3rem;  display:flex;flex-wrap: wrap;}
.movie-list li{width:45%;height: 3.5rem;padding-left:0.2rem;}
.movie-list li img{width:100%;height: 2rem;border-radius: 4%;}
.movie-list li p{width: 100%;height:1rem;overflow: hidden;}
.movie-list li a{color: #000}
.my-swipe {
  width: 100%;
  height: 2.2rem;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  background:url("../../assets/img/1.jpg") center center no-repeat;
  color: #fff;
}

.slide2 {
  background:url("../../assets/img/2.jpg") center center no-repeat; 
  color: #000;
}

.slide3 {
  background:url("../../assets/img/3.jpg") center center no-repeat;
  color: #fff;
}
.slide4 {
  background:url("../../assets/img/4.jpg") center center no-repeat;
  color: #fff;
}
.slide5 {
  background:url("../../assets/img/5.jpg") center center no-repeat;
  color: #fff;
}
</style>



