import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import First from '@/components/first/First'
import FirstList from '@/components/first/FirstList'
import FirstDetail from '@/components/first/FirstDetail'
import Music from '@/components/music/Music'
import MusicList from '@/components/music/MusicList'
import MusicDetail from '@/components/music/MusicDetail'
import Cartoon from '@/components/cartoon/Cartoon'
import CartoonList from '@/components/cartoon/CartoonList'
import National from '@/components/national/National'
import NationList from '@/components/national/NationList'

import Game from '@/components/game/Game'
import GameList from '@/components/game/GameList'
import Hot from '@/components/hot/Hot'
import HotList from '@/components/hot/HotList'
import Vip from '@/components/vip/Vip'
import VipList from '@/components/vip/VipList'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/first/firstlist'
    },{
      path:'/first',
      name:'first',
      component:First,
      children:[
        {
          path:'firstlist',
          name:'firstlist',
          component:FirstList,
        },{
          path:'firstdetail/:movieId/',
          name:'firstdetail',
          component:FirstDetail
        }
      ]
    },{
      path:'/music',
      name:'music',
      component:Music,
      redirect: '/music/musiclist',
      children:[{
        path:"musiclist",
        component:MusicList,
      },{
        path:"musicdetail/:channelid",
        component:MusicDetail
      }]
    },{
      path:'/cartoon',
      name:'cartoon',
      component:Cartoon,
      redirect: '/cartoon/cartoonlist',
      children:[{
        path:'cartoonlist',
        name:'cartoonlist',
        component:CartoonList,
      }]
    },{
      path:'/national',
      name:'national',
      component:National,
      redirect:'/national/nationlist',
      children:[{
        path:'nationlist',
        component:NationList,
      }]
    },{
      path:'/game',
      name:'game',
      component:Game,
      redirect: '/game/gamelist',
      children:[{
        path:'gamelist',
        name:'gamelist',
        component:GameList,
      }]
    },{
      path:'/hot',
      name:'hot',
      component:Hot,
      redirect:'/hot/hotlist',
      children:[{
        path:'hotlist',
        component:HotList,
      }]
    },{
      path:'/vip',
      component:Vip,
      redirect:'/vip/viplist',
      children:[{
        path:'viplist',
        component:VipList,
      }]
    }
  ]
})



