<template>
  <div>
    <div class="tv">
      <video src="http://baobab.kaiyanapp.com/api/v1/playUrl?vid=138122&resourceType=video&editionType=high&source=aliyun" 
      width="100%" controls="controls">
      </video>
    </div>      
    <p class="xiangqing">　　不用烤箱就能做出高颜值的蛋糕🍰！橙香浓郁，香甜可口，让人吃完一块忍不住再来一块呢！
    </p>
    <div class="tuijian">
        <h6>更多推荐</h6>
    </div>
 <ul class="tv-list">
      <li v-for="movie in movieList" :key="movie.id" class="movie-con" >
          <router-link :to="'/first/firstdetail/'+movie.id" >
            <div class="fl">
              <div><img :src="movie.data.content.data.cover.feed" ></div>
              <div>
                <p>{{movie.data.header.title}}</p>
                <p>{{movie.data.header.description}}</p>
              </div>
            </div>       
          </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return{
            movieList:[],
        }
    },
    methods:{
        loadData(){
          //  let url= "http://baobab.kaiyanapp.com/api/v1/playUrl?vid=138122&resourceType=video&editionType=high&source=aliyun"
            let url2="./static/data/shipin.json"
            Axios.get(url2).then((res)=>{
                this.movieList=res.data.result;

            }).catch(()=>
                 alert("失败")
            );
        }
    },
    mounted() {
        this.loadData();
    },
    
}
</script>

<style scoped>
.xiangqing{margin:0 0.1rem 0.1rem 0.1rem;color:#555; font-size: 0.07rem;}
.tuijian{width: 100%;border-bottom: 1px solid #4D4D4D;}
.tuijian{color: #FA7198;padding-left: 0.2rem;}
.tv{margin-top:0.2rem;width: 100%;height: 4rem;}
.tv-list{width: 90%;padding-left: 5%;margin-bottom:1.5rem;}
.tv-list li{width: 100%;height:1.5rem;padding: 0.2rem 0;border-bottom:1px solid #DADADA;}
.tv-list li a{color: black;}
.movie-con a{color: #222;}
.fl{display: flex;}
.fl div{flex: 1 1 0;margin-right: 0.2rem;}
.fl div img{height: 1.5rem;}
.fl p{color: #444; font-size: 0.6em;} /* */
</style>



