<template>
  <div class="header">
    <div >
      <img src="/static/img/liebiao.png"  @click="showSide" slot="overwrite-left" type="navicon" size="35" style="fill:#fff;position:relative;top:-8px;left:-3px;">
    </div>
    <div>
      <img src="/static/img/bg.jpg">
    </div>
    <div class="title">
      <slot name="title"><img src="/static/img/fdj.png"></slot>
    </div>
    <div>
      <img src="/static/img/xiazai.png">
    </div>
    <div>
      <img src="/static/img/xiaoxi.png">
    </div>
  </div>
</template>
<script>
export default {
    methods: {
    showSide(){
      this.$store.dispatch('showSideBar')
    }
  },
  mounted(){
  }
}
</script>
<style scoped>
.header{width: 100%;height: 1rem;margin: auto;background: #FA7198;display: flex;position: fixed;top: 0;z-index:1000;}
.header div{margin:0.2rem 0;}
.header div img{width: 0.5rem;}
.header div:nth-child(1){flex: 0.2;margin:0.4rem 0 0 0.1rem;text-align: center;}
.header div:nth-child(2){flex: 0.3 ;margin:0.1rem 0;}
.header div:nth-child(2) img{width: 0.8rem;height: 0.8rem;border-radius: 50%;overflow: hidden;}
.header div:nth-child(3){margin-left: 0.1rem; flex:1;background: #E4678B;border-radius: 5%;overflow: hidden;}
.header div:nth-child(3) img{width: 0.4rem;padding:0.1rem 0 0 0.1rem;}
.header div:nth-child(4){flex: 0.2;text-align: right;}
.header div:nth-child(5){flex: 0.2;text-align: right;}
</style>



