<template>
    <div>
      <ul class="movie-list">
      <li v-for="movie in movieList" :key="movie.aid" class="movie-con">
        <div>
          <router-link :to="'/first/firstdetail/'+movie.id">
            <img :src="movie.img">
            <p>{{movie.scm}}</p>
          </router-link>
        </div>
      </li>
    </ul>  
    </div>
</template>
<script>
import Axios from 'axios'
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";
export default {
    data(){
        return{
            movieList:[],
        }
    },
    methods:{
        loadData(){
            let url1="./static/data/moviedata.json";
            Axios.get(url1).then((res)=>{
                this.movieList=res.data.data.movies
                console.log(this.movieList)
            }).catch(()=>
                 alert("失败")
            );
        }
    },
    mounted() {
        this.loadData();
    }
}
</script>

<style scoped>
.movie-list{margin-top: 0.2rem;width: 96%;margin: 0 auto;}
.movie-list img{width: 100%;}
li a{color:#000;}
</style>



