<template>
  <div>
    <swipe class="my-swipe">
      <swipe-item class="slide1"></swipe-item>
      <swipe-item class="slide2"></swipe-item>
      <swipe-item class="slide3"></swipe-item>
      <swipe-item class="slide4"></swipe-item>
      <swipe-item class="slide5"></swipe-item>
      <swipe-item class="slide6"></swipe-item>
    </swipe>
    <!-- <ul class="fenlei">
        <li> 
            <img src="">
            分类
        </li>
    </ul>  -->
    <p class="nv">女频推荐</p>
    <ul class="book-list">
      <li v-for="book in bookList" :key="book.bid" class="book-con">
        <div>
          <router-link to="">
            <img :src="book.book_cover">
            <p>{{book.bookname}}</p>
            <p>[{{book.author_name}}]</p>
          </router-link>
        </div>
      </li>
    </ul>
    <ul class="book-list">
      <p class="nv">男频推荐</p>
      <li v-for="book2 in bookList2" :key="book2.authorid" class="book-con">
        <div>
          <router-link to="">
            <img :src="book2.cover">
            <p>{{book2.title}}</p>
            <p>{{book2.author}}</p>
          </router-link>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Axios from 'axios'
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";

export default {
    components:{
        "swipe": Swipe,
        "swipe-item": SwipeItem
    },
    data(){
        return{
            bookList:[],
            bookList2:[]
        }
    },
    methods:{
        loadData(){
            let url="./static/data/xiaoshuo.json"
            let url2="./static/data/xiaoshuo2.json"
            Axios.get(url).then((res)=>{
                this.bookList=res.data.data;
                console.log(this.bookList)
            }).catch(()=>
                 alert("失败")
            );
            Axios.get(url2).then((res)=>{
                this.bookList2=res.data.data.data;
                console.log(this.bookList2)
            }).catch(()=>
                 alert("失败")
            );
        }
    },
    mounted () {
        this.loadData();
    }
}
</script>

<style scoped>
.my-swipe {
  width: 100%;
  height: 3rem;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  background:url("/static/img/shu (2).jpg") center center no-repeat;
  background-size:cover;
  color: #fff;
}

.slide2 {
  background:url("/static/img/xiaoshuo (1).jpg") center center no-repeat; 
  color: #000;
  background-size:cover;

}

.slide3 {
  background:url("/static/img/shu (3).jpg") center center no-repeat;
  color: #fff;
  background-size:cover;

}
.slide4 {
  background:url("/static/img/xiaoshuo (2).jpg") center center no-repeat;
  color: #fff;
  background-size:cover;
}
.slide5 {
  background:url("/static/img/xiaoshuo (3).jpg") center center no-repeat;
  color: #fff;
  background-size:cover;
}.slide6 {
  background:url("/static/img/xiaoshuo (4).jpg") center center no-repeat;
  color: #fff;
  background-size:cover;
}
.nv{width: 100%;height:1rem;line-height: 1rem;}
.book-list{width: 100%;display: flex;flex-wrap:wrap;justify-content: space-around;}
.book-con{width:26%;}
.book-con a{color: #222;font-weight: normal;outline: none;}
.book-con p:nth-child(2){font-size: 0.08rem;}
</style>



