<template>
    <div>
        <div class="cartoon-list">
            <h5>
                <p>热门推荐 </p> 
                <p class="change">
                <img src="/static/img/paihang.png"> 排行榜
                &gt;
                </p>
            </h5>
            <ul>
                <li v-for="movie in movieList" :key="movie.aid" class="movie-con">
                    
                <img :src="movie.img">
                <p>{{movie.scm}}</p>
                </li>

            </ul>
            <h5>
                <p>MAD-AMV</p> 
                <p>查看更多&gt;</p>
            </h5>
            <ul>
                <li v-for="movie in movieList1" :key="movie.aid" class="movie-con">
                <img :src="movie.img">
                <p>{{movie.scm}}</p>
                </li>

            </ul>
            <h5>
                <p>MMD-3D</p> 
                <p>查看更多&gt;</p>
            </h5>
            <ul>
                <li v-for="movie in movieList2" :key="movie.aid" class="movie-con">
                <img :src="movie.img">
                <p>{{movie.scm}}</p>
                </li>

            </ul>

        </div>
    </div>
</template>

<script>
import Axios from 'axios'
export default {
  data(){
    return {
      movieList:[],
      movieList1:[],
      movieList2:[],

      } 
  },
  methods:{
    loadData(){
        // let url1=API_PROXY + "http://api.douban.com/v2/movie/top250"
        let url2 = "./static/data/cartoon.json"
        Axios.get(url2).then((res)=>{ 
        let list=res.data.data.movies;
        console.log(res);
       this.movieList=list.slice(0,4);
       this.movieList1=list.slice(4,8);
       this.movieList2=list.slice(8,12);

      }).catch(()=>{
        alert('数据接收失败')
      }

      )
    },
  },
    mounted() {
      this.loadData();
    } 
}
</script>

<style scoped>
.cartoon-list{margin-top: 0.2rem;}
.cartoon-list h5{width: 94%;height:0.7rem;display: flex;margin: auto;}
.cartoon-list h5 p{width: 48%;}
.cartoon-list h5 .change{color: #FF7878;text-align: right;}
.cartoon-list h5 p:nth-child(2) {text-align: right;color: #999999}
.cartoon-list h5 p img{width: 12%; vertical-align: middle}
.cartoon-list ul{width: 100%; display:flex;flex-wrap: wrap;}
.cartoon-list ul li{width:45%;height: 3.2rem;padding-left:0.2rem;}
.cartoon-list ul li p{width: 100%;height:1rem;overflow: hidden;}

</style>



