<template>
<div> 
    <div class="bg"><img src="/static/img/m-bg.jpg" alt=""></div>
    <div class="music-album">
        <!-- <h2>{{$route.params.musicId}}</h2> -->
        <aplayer autoplay :list="songs" :showLrc="islrc"
            :music="{
            title: 'secret base~君がくれたもの~',
            artist: 'Silent Siren',
            src: 'https://moeplayer.b0.upaiyun.com/aplayer/secretbase.mp3',
            pic: 'https://moeplayer.b0.upaiyun.com/aplayer/secretbase.jpg',
            lrc:'https://api.bzqll.com/music/netease/lrc?key=579621905&id=526307800'
            }"
        />
    </div>
</div>
    
</template>

<script>
import Aplayer from 'vue-aplayer'
 import Axios from 'axios'

export default {
    components: {
        Aplayer
    },
    data(){
        return{
            songs:[],
            islrc:false
        }
    },
    mounted () {
        let url="/static/data/musicdata.json"
        let list=[]
        Axios.get(url).then(res=>{
            list=res.data.musicData
            list.forEach(tune => {
                this.songs.push({
                    title: tune.title,
                    artist:tune.author,
                    src:tune.src,
                    pic:tune.musicImgSrc ,
                    lrc:tune.lrc,
                })
            });
            console.log(list)
            this.islrc=true
        }).catch()
    },
    props:['showLrc']
}
</script>
<style scoped>
/* .music-album{margin: 1rem 0;} */
/* .bg{width: 100%; background: url(/static/img/m-bg.jpg) no-repeat;margin-top: 1.5rem;} */
</style>



