<template>
    <div class="home">
    <transition name="fade" @touchmove.stop.prevent>
      <!-- 这部分功能主要是关闭侧栏菜单，类似一层遮罩的效果 -->
      <div class="menu-mask" v-show="isRellyShow" @click="hideSide"></div>
    </transition>
      <transition name="slide-fade">
      <!-- 这里才是侧栏代码部分 -->
        <div class="side-content" v-show="isRellyShow">
          <div class="top">
              <div>
                  <img src="/static/img/bg.jpg" class="images">
                  <p>点击更换头像</p> 
              </div>
              <div><img src="/static/img/saoma.png"></div>
          </div>
          <ul class="navlist">
              <li><img src="/static/img/first.png" alt="">首页</li>
              <li><img src="/static/img/lishi.png" alt="">历史记录</li>
              <li><img src="/static/img/lixian.png" alt="">离线缓存</li>
              <li><img src="/static/img/shoucang.png" alt="">我的收藏</li>
              <li><img src="/static/img/shaohou.png" alt="">稍后在看</li>
              <li><img src="/static/img/zhibo.png" alt="">直播中心</li>
              <li><img src="/static/img/dingdan.png" alt="">我的订单</li>
              <li><img src="/static/img/kefu.png" alt="">联系客服</li>             
          </ul>
          <ul class="footerlist">
              <li><img src="/static/img/shezhi.png" alt=""><span>设置</span> </li>
              <li><img src="/static/img/zhuti.png" alt=""><span>主题</span>  </li>
              <li><img src="/static/img/yejian.png" alt=""><span>夜间</span> </li>
          </ul>
      </div>    
    </transition> 
  </div>
</template>
<script>
export default {
  components:{},
  data () {
    return {
    
      }
  },
 methods: {
  hideSide:function(){
  this.$store.dispatch('hideSideBar');
  }
  },
  computed:{
  isRellyShow(){
  return this.$store.getters.isShowMethod;
  }
  },
  mounted:function(){
  
  }
}
</script>

<style scoped>
.menu-mask{
position:fixed;
top:0;
left:0;
bottom:0;
right:0;
opacity: 1;
z-index:10;
background:rgba(0,0,0,0.5);
}
.side-content{
z-index: 1100;position:fixed;width:70%;
height: 100%;
background: #e5e5e5;
top: 0;
left: 0;
bottom: 0;
-webkit-overflow-scrolling:touch;
}
.top{
height: 23%;
background: #FA7198;
display: flex;
justify-content: space-around;
padding-top: 0.4rem;
text-align: center;
}
.top div:nth-child(1){
    color:#fff;
}
.top div:nth-child(2) img{
    width: 0.4rem;
    height: 0.4rem;
    padding:0.1rem; 
}
.top .images{
    width:1.4rem;
    height:1.4rem ;
    border-radius: 50%;
    overflow: hidden;

}
.navlist{margin-top: 0.2rem;}
.navlist li{
    width: 100%;
    line-height: 0.9rem   
}
.navlist li:hover{
    color: #FA7198;
    background: #ddd;
}
.navlist li:nth-child(1){
    color: #FA7198;
    background: #ddd;
}
.navlist li:nth-child(6){
    border-top: 1px solid #ccc;
}
.navlist li img{width: 0.3rem;padding: 0.3rem;}
.footerlist{width: 100%;display: flex;justify-content: space-around;height: 1rem;line-height: 1rem;border-top:1px solid #ccc;}
.footerlist li{width: 28%;text-align: center;}
.footerlist li img{width: 0.28rem;vertical-align: middle;border: 1px solid #515151;border-radius:50%;padding: 0.05rem;}
.footerlist li span{padding-left: 0.1rem;}
.footerlist li:hover{color: #FA7198;}
.fade-enter-to,.fade-leave-to{
transition: opacity 0.3s
}
.fade-enter, .fade-leave-to{
opacity: 0
}
.slide-fade-enter-to, .slide-fade-leave-to{
transition: transform 0.3s;
transform:translate(0px,0px);
}
.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
  -webkit-transform: translate(-286px, 0px);
  transform: translate(-286px, 0px);
  -webkit-transition: opacity 0.3s ease-in-out 0.3s, -webkit-transform 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out 0.3s, transform 0.3s ease-in-out;
}
.change {
  display: block;
  width: 284px;
  height: 200px;
  background: url(http://p3.qhimg.com/t0134c65e59012a1257.png) no-repeat center;
  background-size: cover;
  border: 1px solid #fff;
  overflow: hidden;
}
.change img {
  display: block;
  width: 220px;
  height: 180px;
  opacity: 0;
  -webkit-transform: translate(284px, 200px);
  transform: translate(284px, 200px);
  -webkit-transition: opacity 0.5s ease-in-out 0.5s, -webkit-transform 1s ease-in-out;
  transition: opacity 0.5s ease-in-out 0.5s, transform 1s ease-in-out;
}

.change:hover img {
  -webkit-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
  opacity: 1;
}

</style>



