<template>
  <div>
    <ul class="hot-list">
      <li v-for="hot in hotList" :key="hot.aid" class="hot-con">
        <div>
          <video :src="hot.videouri" width="100%" controls="controls" class="mv">
          </video>
        </div>
        <div class="miao">
            <div><img :src="hot.profile_image" alt="" class="tou"></div>
            <p class="des">{{hot.text}}</p>
        </div>
        
        <p class="bo">播放量：{{hot.playcount}}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import Axios from 'axios'
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";
export default {
    data(){
        return{
            hotList:[],
        }
    },
    methods:{
        loadData(){
            let url1="./static/data/dongtai.json";
            Axios.get(url1).then((res)=>{
                this.hotList=res.data.data;
                console.log(this.hotList)
            }).catch(()=>
                 alert("失败")
            );
        }
    },
    mounted() {
        this.loadData();
    }
}
</script>

<style scoped>
.hot-list{margin: 1rem 0;}
.mv{ position: relative;}

.miao{width: 90%;display: flex;margin:0.1rem auto;}
.tou{width: 0.6rem;height: 0.6rem; border-radius:50%;overflow: hidden;}
p.des{width: 80%; height: 0.6rem;overflow: hidden;}
.bo{position:absolute;left:0;top:-0.5rem;}
</style>



