<template>
    <div>
        <div class="channel">
            <p>推荐频道</p>
        </div>
         <ul class="public-list">
            <li v-for="music in musiclist" :key="music.channelid">
                <router-link :to="'/music/musicDetail/'+music.channelid">
                    <img :src="music.thumb">
                    <p>{{music.name}}</p>
                </router-link>
            </li>
        </ul>
        <div class="channel">
            <p>公共频道</p>
        </div>
        <ul class="public-list">
            <li v-for="music in publiclist" :key="music.artistid">
                <router-link :to="'/music/musicdetail/'+music.artistid">
                    <img :src="music.avatar">
                    <p>{{music.name}}</p>
                </router-link>
            </li>
        </ul>

    </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return{
            musiclist:[],
            publiclist:[]
        }
    },
    mounted () {
      let url1="./static/data/gefenlei.json";
      Axios.get(url1).then(res=>{
          let arr=res.data.result
          this.musiclist=arr[0].channellist
          this.publiclist=arr[1].channellist
          console.log(this.publiclist)
          console.log(this.musiclist)

      }).catch()  
    }

}
</script>

<style scoped>
.channel{width: 100%;}
.channel p{padding-left: 0.15rem;background:url(/static/img/tbg.jpg) no-repeat left center;}
.public-list{width: 100%; margin:0.1rem 0 1.2rem 0;display: flex;flex-wrap: wrap;justify-content:space-around;}
.public-list li{width: 46%;}
.public-list li img{width:100%;}

</style>


